"use client"; // IMPORTANTE: permite usar componentes que solo funcionan en el navegador

import { useState } from "react";
import ChessBoardAI from "./ChessBoardAI";

export default function Page() {
  const [botElo, setBotElo] = useState(1200);

  return (
    <div style={{ display: "flex", flexDirection: "column", alignItems: "center", marginTop: "50px" }}>
      <h1>ðŸ§  Juego de Ajedrez con IA</h1>
      
      <div style={{ margin: "20px 0" }}>
        <label>
          Elegir nivel del bot (ELO):{" "}
          <input
            type="range"
            min={500}
            max={2900}
            step={100}
            value={botElo}
            onChange={(e) => setBotElo(parseInt(e.target.value))}
          />
        </label>
        <span style={{ marginLeft: "10px" }}>{botElo}</span>
      </div>

      <ChessBoardAI botElo={botElo} />
    </div>
  );
}
